.order {
  position: relative;
  padding: 121px 0 151px 0;

  @include vp-1023 {
    padding: 60px 0 75px 0;
  }

  .advantages {
    margin-bottom: 80px;

    @include vp-1023 {
      margin-bottom: 40px;
    }
  }

  &__bg-wrapper {
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    background-color: $color-bg;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    margin: 0 0 30px 0;
    font-weight: 900;
    font-size: 36px;
    text-transform: uppercase;
    text-align: center;
    color: $color-default-white;

    @include vp-1023 {
      margin: 0 0 15px 0;
      font-weight: 900;
      font-size: 26px;
    }
  }

  &__text {
    margin:  0 0 80px 0;
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    color: $color-gray-5;

    @include vp-1023 {
      margin: 0 0 40px 0;
      font-size: 16px;
    }
  }
}
